.home-container {
    min-height: 100vh;
    background-color: #f8f9fa;
    color: #2d3436;
}

.home-content {
    padding-top: 4rem; /* Account for fixed navbar */
}

.hero-section {
    position: relative;
    min-height: calc(100vh - 4rem);
    display: flex;
    align-items: center;
    padding: 0 10%;
    background: linear-gradient(to right, #f8f9fa 60%, #e8f0fe 40%);
    justify-content: space-between;
}

.hero-text {
    max-width: 600px;
}

.hero-text h1 {
    font-size: 3.5rem;
    font-weight: 600;
    margin-bottom: 1rem;
    line-height: 1.2;
}

.hero-text h2 {
    font-size: 2.5rem;
    font-weight: 500;
    margin-bottom: 1.5rem;
    color: #4a4a4a;
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
}

.highlight {
    color: #0984e3; /* Blue accent color matching the gif */
}

.subtitle {
    font-size: 1.1rem;
    line-height: 1.6;
    color: #636e72;
    margin-bottom: 2rem;
}

.cta-buttons {
    display: flex;
    gap: 1.5rem;
}

.primary-button, .secondary-button {
    padding: 0.8rem 2rem;
    border-radius: 4px;
    font-weight: 500;
    text-decoration: none;
    transition: all 0.3s ease;
}

.primary-button {
    background-color: #0984e3;
    color: white;
}

.primary-button:hover {
    background-color: #0773c5;
    transform: translateY(-2px);
}

.secondary-button {
    border: 2px solid #0984e3;
    color: #0984e3;
}

.secondary-button:hover {
    background-color: #0984e3;
    color: white;
    transform: translateY(-2px);
}

.timeline-indicator {
    position: absolute;
    right: 5%;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.dot {
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: #dfe6e9;
    transition: all 0.3s ease;
}

.dot.active {
    background-color: #0984e3;
    transform: scale(1.2);
}

/* Skills Section */
.skills-section {
    padding: 6rem 10%;
    background-color: white;
}

.skills-section h3 {
    color: #0984e3;
    font-size: 1.1rem;
    margin-bottom: 0.5rem;
}

.section-title {
    font-size: 2.5rem;
    margin-bottom: 3rem;
    position: relative;
    display: inline-block;
}

.section-title::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: -10px;
    width: 60px;
    height: 3px;
    background-color: #0984e3;
}

.skills-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 2rem;
}

.skill-card {
    background-color: #f8f9fa;
    padding: 2rem;
    border-radius: 8px;
    transition: all 0.3s ease;
}

.skill-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

.skill-icon {
    width: 48px;
    height: 48px;
    margin-bottom: 1.5rem;
    transition: transform 0.3s ease;
}

.skill-card:hover .skill-icon {
    transform: scale(1.1);
}

.skill-card h4 {
    font-size: 1.2rem;
    margin-bottom: 1rem;
    color: #2d3436;
}

.skill-card p {
    color: #636e72;
    line-height: 1.6;
}

/* Responsive Design */
@media (max-width: 768px) {
    .hero-section {
        flex-direction: column;
        padding: 4rem 5%;
        background: #f8f9fa;
        text-align: center;
    }

    .hero-image {
        margin-top: 2rem;
        padding: 0;
    }

    .hero-image img {
        max-width: 80%;
    }

    .hero-text h1 {
        font-size: 2.5rem;
    }

    .hero-text h2 {
        font-size: 2rem;
    }

    .cta-buttons {
        justify-content: center;
    }

    .timeline-indicator {
        display: none;
    }

    .skills-section {
        padding: 4rem 5%;
    }
}

.hero-image {
    flex: 0 0 45%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2rem;
}

.hero-image img {
    max-width: 100%;
    height: auto;
    border-radius: 10px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

/* Add these styles after the hero-text h2 styles */
.typewriter {
    color: #0984e3;
    display: inline-block;
}